@use "./top-mv";
@use "../../base/base" as b;
@use "../../base/mixin" as m;

.scroll-btn.is-active {
  opacity: 1;
  pointer-events: initial;
}

.scroll-btn {
  opacity: 0;
  pointer-events: none;
  z-index: 3;
  position: fixed;
  bottom: 4rem;
  right: 2rem;
  transform: rotate(90deg);
  color: #3e3a39;
  cursor: pointer;
  transition: 0.5s opacity ease;
  @media screen and (max-width: 600px) {
    right: 1.5rem;
  }
  &__text {
    font-size: 1.4rem;
    font-family: "Playfair Display", serif;
    letter-spacing: 0.2em;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      top: -0.65rem;
      right: -0.5rem;
      width: 7.5rem;
      height: 1rem;
      border-top: 1px solid currentColor;
      border-left: 1px solid currentColor;
      transform: skew(35deg);
    }
    @media screen and (min-width: 600px) {
      &::before {
        width: 10rem;
        height: 1.2rem;
      }
    }
  }
}

// arrow-right-btn
.arrow-right-btn {
  display: inline-block;
  text-align: center;
  padding: 2rem 1rem;
  color: #3e3a39;
  font-size: 1.6rem;
  line-height: 1.25;
  font-family: "Playfair Display", serif;
  letter-spacing: 0.2em;
  width: 28rem;
  max-width: 100%;
  border-radius: 30px;
  border: 1px solid currentColor;
  transition: 0.5s ease;
  position: relative;
  &::after {
    content: "";
    display: inline-block;
    position: absolute;
    top: 0;
    right: -3rem;
    bottom: 0;
    margin: auto;
    width: 7rem;
    height: 1rem;
    border-right: 1px solid Currentcolor;
    border-bottom: 1px solid Currentcolor;
    transform: skew(45deg) translateY(-20%);
    transition: 0.7s ease;
  }
  &:focus,
  &:hover {
    color: #fff;
    background: rgba(62, 58, 57, 0.4);
    border-color: rgba(62, 58, 57, 0.4);
    &::after {
      transform: skew(45deg) translate(4rem, -20%);
      opacity: 0;
    }
  }
}

// lv1-heading
.lv1-heading {
  &__title {
    color: #fff;
    font-size: clamp(4.5rem, 4.0625vw, 8.5rem);
    font-family: "Playfair Display", serif;
    font-weight: bold;
    letter-spacing: 0.2em;
  }
  &__text {
    color: #fff;
    font-size: clamp(1.6rem, 1.25vw, 4rem);
    margin-top: 1.5rem;
    text-align: center;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
    }
  }
}

// low-lv1-heading
.low-lv1-heading {
  font-size: 3rem;
  font-weight: bold;
  font-family: "Playfair Display", serif;
  letter-spacing: 0.1em;
  @media screen and (min-width: 600px) {
    font-size: 3.5rem;
  }
}

// lv2-heading
.lv2-heading {
  &__title {
    font-size: 3rem;
    font-weight: bold;
    font-family: "Playfair Display", serif;
    letter-spacing: 0.1em;
    @media screen and (min-width: 600px) {
      font-size: 3.5rem;
    }
  }
  &__title--border-right {
    text-align: right;
    position: relative;
    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      right: calc(((100vw - 108rem) / 2 + 20px + 2rem) * -1);
      height: 1px;
      width: calc((100vw - 108rem) / 2 + 20px + 2rem - 1vw);
      transform: translateY(-50%);
      background: currentColor;
    }
  }
  &__title--border-left {
    position: relative;
    &::before {
      content: "";
      position: absolute;
      display: block;
      top: 50%;
      left: calc(((100vw - 108rem) / 2 + 20px + 2rem) * -1);
      height: 1px;
      width: calc((100vw - 108rem) / 2 + 20px + 2rem - 1vw);
      transform: translateY(-50%);
      background: currentColor;
    }
  }
  &__text {
    margin-top: 2rem;
    font-size: 1.8rem;
    line-height: 1.5;
    @media screen and (min-width: 600px) {
      font-size: 2rem;
    }
  }
}

// lv3-heading
.lv3-heading {
  &__title {
    font-size: 1.8rem;
    font-weight: bold;
    line-height: 1.5;
    @media screen and (min-width: 600px) {
      font-size: 2rem;
      line-height: 1.2;
    }
  }
}

// container
.container {
  max-width: 108rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}

//  low-container
.low-container {
  max-width: 90rem;
  margin-right: auto;
  margin-left: auto;
  padding-right: 2rem;
  padding-left: 2rem;
}

// grid-list
.grid-list {
  display: grid;
  gap: 3rem;
  @media screen and (min-width: 600px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (min-width: 900px) {
    grid-template-columns: repeat(3, 1fr);
  }
}

// grid
.grid {
  opacity: 0;
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
  &__content {
    display: flex;
    flex-direction: row-reverse;
    padding: 2rem;
    height: 100%;
    @media screen and (min-width: 600px) {
      flex-direction: column-reverse;
    }
  }
  &__body {
    flex-grow: 1;
    margin-left: 3rem;
    display: flex;
    flex-direction: column;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
      margin-left: 0;
    }
  }
  &__title {
    font-size: 2rem;
    font-weight: bold;
    @media screen and (min-width: 600px) {
      text-align: center;
    }
  }
  &__text {
    margin-top: 1.5rem;
    font-size: 1.6rem;
    line-height: 1.5;
    @media screen and (min-width: 600px) {
      margin-top: 2rem;
    }
  }
  &__img {
    width: 6.5rem;
    flex-shrink: 0;
    margin-right: auto;
    margin-left: auto;
    position: relative;
    @media screen and (min-width: 600px) {
      width: 8rem;
      margin-top: 2rem;
    }
    &::before {
      content: "";
      display: block;
      padding-top: 100%;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      max-width: none;
      object-fit: cover;
    }
  }
}
@keyframes op {
  0% {
    opacity: 0;
    transform: translateY(-50px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}

.grid.op {
  position: relative;
  @include m.animation($name: op, $duration: 2.3s, $fill-mode: both);
  @for $i from 1 through 9 {
    &:nth-of-type(#{$i}) {
      animation-delay: $i * 0.4s;
    }
  }
  @media screen and (min-width: 600px) {
    @for $i from 1 through 9 {
      &:nth-of-type(#{$i}) {
        animation-delay: $i * 0.2s;
      }
    }
  }
}

// swiper Block
.swiper-slide {
  max-width: 32rem;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  @media screen and (min-width: 768px), print {
    margin-right: 2rem;
    margin-left: 2rem;
  }
}

// card
.card {
  display: block;
  display: flex;
  flex-direction: column-reverse;
  &__body {
    margin-top: 2rem;
  }
  &__skill {
    font-size: 1.4rem;
    color: #f4f4f4;
    letter-spacing: 0.1em;
    word-break: break-all;
  }
  &__title {
    color: #3e3a39;
    // margin-top: 1.5rem;
    font-size: 1.8rem;
  }
  &__description {
    margin-top: 5rem;
  }
  &__about {
    color: #3e3a39;
    font-size: 1.6rem;
    font-weight: bold;
  }
  &__sub-title {
    margin-top: 2rem;
    color: #3e3a39;
    font-size: 1.6rem;
    line-height: 1.5;
  }
  &__text {
    margin-top: 1rem;
    color: #3e3a39;
    font-size: 1.6rem;
    line-height: 1.75;
  }
  &__time {
    color: #3e3a39;
    margin-top: 5rem;
    font-size: 1.6rem;
  }
  &__link {
    color: #3e3a39;
    font-size: 1.6rem;
    margin-top: 3.5rem;
    word-break: break-all;
    text-decoration: underline;
  }
  &__img {
    position: relative;
    &::before {
      content: "";
      display: block;
      padding-top: 100%;
    }
    &::after {
      content: "VIEW MORE";
      position: absolute;
      z-index: 2;
      top: 0;
      left: 0;
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      width: 100%;
      color: #3e3a39;
      font-size: 1.6rem;
      font-family: "Playfair Display", serif;
      letter-spacing: 0.2em;
      background: rgba(255, 255, 255, 0.7);
      opacity: 0;
      transform: translate(0, 1rem);
      transition: all 0.5s ease;
    }
    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
      object-position: top;
      border-radius: 3px;
      box-shadow: 0 3px 6px rgba(0, 0, 0, 0.16);
    }
    &:focus,
    &:hover {
      &::after {
        opacity: 1;
        transform: none;
      }
    }
  }
}

.card--link-none {
  .card__img {
    &::after {
      display: none;
    }
  }
}

.works-list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  margin-top: -6rem;
  @media screen and (min-width: 600px) {
    flex-direction: row;
    margin-left: -5rem;
  }
  &__item {
    margin-top: 6rem;
    max-width: 60rem;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    @media screen and (min-width: 600px) {
      margin-left: 5rem;
      width: calc((100% - 10rem) / 2);
    }
  }
}

// lead Section
.lead {
  padding-top: 5rem;
  @media screen and (min-width: 600px) {
    padding-top: 7rem;
  }
  &__title-text {
    margin-top: 4rem;
    text-align: center;
    @media screen and (min-width: 600px) {
      font-size: 4rem;
      margin-top: 5rem;
    }
  }
  &__body {
    margin-top: 3rem;
    @media screen and (min-width: 600px) {
      text-align: center;
    }
  }
  &__text {
    font-size: 1.6rem;
    line-height: 1.5;
  }
  &__text + &__text {
    margin-top: 2rem;
  }
}
.sm-only {
  display: block;
  @media screen and (min-width: 600px) {
    display: none;
  }
}

// skills Section
.skills {
  padding-top: 7rem;
  @media screen and (min-width: 600px) {
    padding-top: 10rem;
  }
  &__title {
    text-align: center;
  }
  &__body {
    margin-top: 4rem;
    @media screen and (min-width: 600px) {
      margin-top: 5rem;
    }
  }
  &__note-text {
    margin-top: 4rem;
    text-align: right;
    font-size: 1.6rem;
    font-weight: bold;
  }
}

// works Section
.works {
  padding-top: 9rem;
  @media screen and (min-width: 600px) {
    padding-top: 11rem;
  }
  &__title {
    text-align: center;
  }
  &__body {
    margin-right: calc(50% - 50vw);
    margin-left: calc(50% - 50vw);
    width: 100vw;
    margin-top: 4.5rem;
    @media screen and (min-width: 768px), print {
      margin-top: 6rem;
    }
  }
  &__btn-wrapper {
    text-align: center;
    margin-top: 8rem;
  }
}


// Swiper設定
.swiper + .swiper {
  margin-top: 10rem;
}
.swiper-wrapper {
  transition-timing-function: linear;
}

.swiper-works1 .swiper-slide {
  max-width: 32rem;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  @media screen and (min-width: 768px), print {
    margin-right: 2rem;
    margin-left: 2rem;
  }
}

.swiper-works2 .swiper-slide {
  max-width: 28rem;
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  @media screen and (min-width: 768px), print {
    margin-right: 1.5rem;
    margin-left: 1.5rem;
  }
}